<template>
  <div>
    <!-- 查询 -->
    <common-wrapper>
      <search
        :warehouseList="warehouseList"
        :merchantsList="merchantsList"
        :modelBrand="modelBrand"
        :brandList="brandList"
        :allModelList="modelList"
        @getMerchants="getMerchantsList"
        @getBrandModel="getBrandModel"
        @search="queryList"
        @rest="rest" />
    </common-wrapper>
    <!-- 按钮 -->
    <nav-button>
      <el-button-group>
        <el-button icon="el-icon-download" @click="exportDetail">导出明细</el-button>
      </el-button-group>
    </nav-button>
    <!-- 订单列表 -->
    <common-wrapper>
      <common-table :flex="230">
        <template slot="table">
          <el-table
            ref="dataTable"
            height="100%"
            stripe
            v-loading="isListLoading"
            :data="tableData.slice((pagination.pageNum-1)*pagination.pageSize,pagination.pageNum*pagination.pageSize)">
            <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
            <el-table-column sortable prop="warehouseName" label="项目"></el-table-column>
            <el-table-column sortable prop="userRealName" label="商户"></el-table-column>
            <el-table-column sortable prop="orderDate" label="日期" min-width="100"></el-table-column>
            <el-table-column sortable v-if="showEntry" prop="entryOrderCount" label="入库单数"></el-table-column>
            <el-table-column sortable v-if="showEntry" prop="entryGoodsCount" label="入库件数"></el-table-column>
            <el-table-column sortable v-if="showDelivery" prop="deliveryOrderCount" label="出库单数"></el-table-column>
            <el-table-column sortable v-if="showDelivery" prop="deliveryGoodsCount" label="出库件数"></el-table-column>
          </el-table>
        </template>
        <template slot="pagination">
          <el-pagination   background   layout="total, prev, pager, next"
                           :current-page.sync="pagination.pageNum"
                           :page-size="pagination.pageSize"
                           :total="pagination.total"
                           @current-change="changePage"
          ></el-pagination>
        </template>
      </common-table>
    </common-wrapper>
  </div>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import CommonTable from '@/components/commonTable'
  import NavButton from '@/components/navButton'
  import { setSession, getSession } from '@/utils/session'
  import Search from './Components/Search'

  import { getAllMerchantsList,getBrandModel } from '@/api/common'
  import { getOrderRepore, orderExport } from '@/api/datareport'
  export default {
    name: 'Regular',
    data () {
      return {
        warehouseList: this.$store.getters.warehouseList,
        merchantsList: [],
        brandList: [],
        modelBrand: [],
        modelList: [],
        tableData: [],
        isListLoading: false, // 列表加载
        pagination: { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
        },
        searchParams: {}, // 查询参数,
        showEntry: true,
        showDelivery: true
      }
    },
    mounted () {
      this.getMerchantsList()
      this.getBrandModel({warehouseUuid:getSession('warehouseUuid')})
    },
    methods: {
      // 商户列表
      getMerchantsList (params={warehouseUuid: getSession('warehouseUuid')? getSession('warehouseUuid') :  this.warehouseList[0].warehouseUuid,userUuid: ""}) {
        return new Promise(resolve => {
          getAllMerchantsList(params).then(res => {
            if(!res.error) {
              this.merchantsList = res
              resolve()
            }
          })
        })
      },
      // 获取品牌型号列表
      getBrandModel (params) {
        getBrandModel(params).then(res => {
          if (!res.error) {
            this.brandList = []
            this.modelList = []
            res.forEach((item,index) => {
              this.brandList.push(item.productBrand)
              item.productModel.forEach(item2 => {
                this.modelList.push(item2)
              })
            })
            this.modelBrand = res
          }
        })
      },
      // 查询和初始化查询
      queryList (params) {
        const searchParams = this.searchParams = params
        this.getTable(searchParams)
      },
      rest (params) {
        const searchParams = this.searchParams = params
        this.getTable(searchParams)
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination.pageNum = pageNum
      },
      // 生成列表序号
      indexMethod (index) {
        return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
      },
      getTable (params) {
        this.isListLoading = true
        getOrderRepore(params).then( res => {
          if (!res.error) {
            this.tableData = res
            this.pagination.total = res.length
            if (this.searchParams.orderType === '2') {
              this.showDelivery = false
              this.showEntry = true
            } else if (this.searchParams.orderType === '1') {
              this.showEntry = false
              this.showDelivery = true
            } else {
              this.showDelivery = true
              this.showEntry = true
            }
            this.isListLoading = false
          }
        })
      },
      exportDetail () {
        let params = this.searchParams ;
        orderExport(params).then((res) => { // 处理返回的文件流
          const content = res.data
          const blob = new Blob([content], {type: 'application/ms-excel'})
          let fileTitle = params.orderType == 0? '出入库' : params.orderType == 1? '出库' :params.orderType == 2? '入库' : ''
          const fileName = fileTitle+'订单明细'+moment(new Date()).format('YYYYMMDD')+'.xls'
          if ('download' in document.createElement('a')) { // 非IE下载
            const elink = document.createElement('a')
            elink.download = fileName
            elink.style.display = 'none'
            elink.href = URL.createObjectURL(blob)
            document.body.appendChild(elink)
            elink.click()
            URL.revokeObjectURL(elink.href) // 释放URL 对象
            document.body.removeChild(elink)
          } else { // IE10+下载
            navigator.msSaveBlob(blob, fileName)
          }
        })
      }
    },
    components: {
      Search,
      CommonWrapper,
      CommonTable,
      NavButton
    }
  }
</script>

<style lang="scss" scoped>

</style>



