<template>
  <el-form :model="searchForm" ref="searchForm"  label-width="50px" class="dataSearchForm">
    <el-row>
      <el-col :span="4">
        <el-form-item label="仓库" prop="warehouseUuid">
          <el-select v-model="searchForm.warehouseUuid" class="w100p" filterable  @change="getMerchantsList" placeholder="请选择仓库">
            <el-option
              v-for="(item, index) in warehouseList"
              :key="index"
              :label="item.warehouseName"
              :value="item.warehouseUuid">

            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="商户"  prop="userUuid" >
          <el-select v-model="searchForm.userUuid" @change="getBrandList" class="w100p" filterable clearable placeholder="请选择商户">
            <el-option
              v-for="(item, index) in merchantsList"
              :key="index"
              :label="item.userName"
              :value="item.userUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="品牌" prop="brandId">
          <el-select clearable v-model="searchForm.brandId" class="w100p" filterable  placeholder="请选择品牌">
            <el-option
              v-for="(item, index) in brandList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="类型" prop="brandId">
          <el-select v-model="searchForm.orderType" class="w100p" filterable placeholder="请选择类型">
            <el-option label="全部" value="0"></el-option>
            <el-option label="出库" value="1"></el-option>
            <el-option label="入库" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
            <el-col :span="4">
        <el-form-item label="开始日期" label-width="80px" prop="beginDate">
            <el-date-picker
              class="w100p"
              v-model="searchForm.beginDate"
              type="date"
              align="right"
              clearable
              unlink-panels
              range-separator="至"
              placeholder="请选择开始日期"
              format="yyyy 年 MM 月 dd 日"
              value-format="yyyy-MM-dd"
              size="mini"
              >
            </el-date-picker>
          </el-form-item>
      </el-col>
      <el-col :span="4">
          <el-form-item label="结束日期" label-width="80px" prop="endDate">
            <el-date-picker
              class="w100p"
              v-model="searchForm.endDate"
              type="date"
              align="right"
              clearable
              unlink-panels
              range-separator="至"
              placeholder="请选择结束日期"
              format="yyyy 年 MM 月 dd 日"
              value-format="yyyy-MM-dd"
              size="mini"
              >
            </el-date-picker>
          </el-form-item>
      </el-col>
      <el-col :span="4" class="text-right">
        <el-button type="primary" @click.native="search">查询</el-button>
        <el-button @click="resetForm('searchForm')">重置</el-button>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
  import { setSession, getSession } from '@/utils/session'
  import moment from 'moment'
  export default {
    name: 'Search',
    data() {
      return {
        searchForm: {
          warehouseUuid: '',
          orderType: '0',
          userUuid: '',
          brandId: '',
          dateRange: [],
          beginDate:'',
          endDate:'',
        }
      }
    },
    props: {
      warehouseList: {
        type: Array
      },
      merchantsList: {
        type: Array
      },
      brandList: {
        type: Array
      },
      allModelList: {
        type: Array
      },
      modelBrand: {
        type: Array
      }
    },
    mounted () {
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.searchForm.dateRange = [moment().subtract(1, 'days').utcOffset(8).format('YYYY-MM-DD'),moment().utcOffset(8).format('YYYY-MM-DD')]
      this.$emit("search", Object.assign({}, this.searchForm))
    },
    methods: {
      getMerchantsList (value) {
        setSession('warehouseUuid',value)
        this.searchForm.brandId = ''
        this.searchForm.userUuid = ''
        this.$emit('getMerchants', Object.assign({},this.searchForm))
        this.$emit('getBrandModel', {warehouseUuid:this.searchForm.warehouseUuid})
      },
      getBrandList () {
        this.searchForm.brandId = ''
        this.$emit('getBrandModel', {warehouseUuid:this.searchForm.warehouseUuid,merchantUuid:this.searchForm.userUuid})
      },
      // 检索
      search () {
        if (this.searchForm.endDate && this.searchForm.beginDate &&  ( moment(this.searchForm.endDate).isBefore(this.searchForm.beginDate) || moment(this.searchForm.endDate).isSame(this.searchForm.beginDate))) {
              this.$message({
                message: '结束日期不能早于开始日期，请重新选择！',
                type: 'warning'
              })
              this.searchForm.endDate = null ;
              return
      }else{
        this.$emit('search', Object.assign({}, this.searchForm))
      }
      },
      // 重置
      resetForm (formName) {
        this.$refs[formName].resetFields()
        this.searchForm.warehouseUuid = getSession('warehouseUuid')
        this.$emit('rest', Object.assign({}, this.searchForm))
      }
    }
  }
</script>

<style lang="scss" scoped>
  .text-right{
    text-align: right;
  }
  .el-form-item{
    margin-bottom: 10px;
  }
  .el-icon-date{
    display: none !important;
  }
</style>



